import React from 'react';
import Controls from "./Controls";
import Desk from './Desk';
import Realtime from './modules/realtime';
import {RESPONSE_EVENT, RESPONSE_PAYLOAD} from './modules/events';
import LevelPassword from './LevelPassword';
import MinesCounter from "./MinesCounter";
import HelpInformer from "./HelpInformer";

const offsets = [{
    i: -1,
    j: -1
},
    {
        i: -1,
        j: 0
    },
    {
        i: -1,
        j: 1
    },
    {
        i: 0,
        j: 1
    },
    {
        i: 1,
        j: 1
    },
    {
        i: 1,
        j: 0
    },
    {
        i: 1,
        j: -1
    },
    {
        i: 0,
        j: -1
    }];

function findElement(matrix, i, j, value, strict) {
    if (i < 0 || j < 0 || i >= matrix.length || j >= matrix[i].length) {
        return null;
    }
    let parsed = matrix[i][j];
    if ((!value && isNaN(parsed)) || (!isNaN(parsed) && (strict ? parsed === value : parsed >= value))) {
        return {
            row: i,
            cell: j
        }
    }
    return null;
}

function getSiblings(matrix, i, j, value, strict = true) {
    let mines = [],
        mine;
    offsets.forEach(offset => {
        mine = findElement(matrix, i + offset.i, j + offset.j, value, strict);
        if (mine) {
            mines.push(mine);
        }
    });

    return mines;
}

function setupMap(old_matrix) {

    let matrix = old_matrix.map(row => [...row]);
    matrix.forEach((row, rowIndex) => {
        return row.forEach((cell, cellIndex) => {
            let parsed = parseInt(cell);
            if (!isNaN(parsed) && parsed > CELL_FLAG.FLAG) {
                let new_flags = getSiblings(matrix, rowIndex, cellIndex);
                new_flags.forEach(flag => {
                    matrix[flag.row][flag.cell] = CELL_FLAG.FLAG;
                });
                let exist_flags = getSiblings(matrix, rowIndex, cellIndex, CELL_FLAG.FLAG),
                    real_mines = getSiblings(matrix, rowIndex, cellIndex, CELL_FLAG.MINE);
                if (exist_flags.length > 0 && (exist_flags.length + real_mines.length) === parsed) {
                    exist_flags.forEach(flag => {
                        matrix[flag.row][flag.cell] = CELL_FLAG.MINE;
                    });
                }

                if (real_mines.length > 0 && real_mines.length === parsed && exist_flags.length > 0) {
                    exist_flags.forEach(flag => {
                        matrix[flag.row][flag.cell] = CELL_FLAG.SAFE_TO_OPEN;
                    });
                }
            }
        })
    });
    matrix.forEach((row, rowIndex) => {
        return row.forEach((cell, cellIndex) => {
            let parsed = parseInt(cell);
            if (!isNaN(parsed) && parsed > CELL_FLAG.FLAG) {
                let exist_flags = getSiblings(matrix, rowIndex, cellIndex, CELL_FLAG.FLAG),
                    real_mines = getSiblings(matrix, rowIndex, cellIndex, CELL_FLAG.MINE);

                if (real_mines.length > 0 && real_mines.length === parsed && exist_flags.length > 0) {
                    exist_flags.forEach(flag => {
                        matrix[flag.row][flag.cell] = CELL_FLAG.SAFE_TO_OPEN;
                    });
                }
            }
        })
    });
    return matrix;
}


const CELL_FLAG = {
    FLAG: -1,
    MINE: -2,
    REAL_MINE: '*',
    SAFE_TO_OPEN: -3,
    UNOPENED: 'â–¡'
}
class App extends React.Component {

    static displayName = 'App';

    constructor(props) {
        super(props);
        this.state = {
            matrix: [],
            connection: new Realtime('wss://hometask.eg1236.com/game1/', this.resolver),
            started: false,
            loose: false,
            helpRequired: true,
            autobotEnabled: false,
            password: null,
            connected: false
        };
    }

    resolver = (event, payload) => {
        if (event === RESPONSE_EVENT.NEW) {
            if (payload === RESPONSE_PAYLOAD.CONNECTED) {
                this.setState({connected: true});
                return;
            }
            if (payload === RESPONSE_PAYLOAD.OK) {
                this.setState({started: true, matrix: [], helpRequired: false}, () => {
                    this.state.connection.getMap();
                });
                return;
            }
        }
        if (event === RESPONSE_EVENT.MAP) {
            let matrix = payload.split('\n').filter(line => line !== '').map(line => line.split(''));
            if (this.state.matrix.length === matrix.length) {
                matrix.forEach((row, rowIndex) => {
                    row.forEach((cell, cellIndex) => {
                        if (cell === CELL_FLAG.UNOPENED) {
                            matrix[rowIndex][cellIndex] = this.state.matrix[rowIndex][cellIndex];
                        }
                    })
                });
            }
            this.setState({matrix: matrix, helpRequired: false});
            return;
        }
        if (event === RESPONSE_EVENT.OPEN) {
            if (payload === RESPONSE_PAYLOAD.LOSE) {
                this.setState({started: false});
                return;
            }
            if (payload.startsWith(RESPONSE_PAYLOAD.WIN)) {
                this.setState({
                    password: payload,
                    matrix: [],
                    started: false
                });
            }
        }
    };

    restartGame = () => {
        this.setState({
            matrix: [],
            started: false,
            helpRequired: false,
            password: null
        });
    };

    startGame = (level, autobotEnabled, autobotStopPercent) => {
        this.setState({autobotEnabled, autobotStopPercent});
        this.state.connection.createGame(level);
    };
    open = (cell, row) => {
        this.state.connection.openCell(cell, row);
        this.state.connection.getMap();
    };
    openList = (points) => {
        points.forEach(point => {
            this.state.connection.openCell(point.cell, point.row);
        });
        this.state.connection.getMap();
    };
    setFlag = (row, cell) => {
        const matrix = this.state.matrix.map(row => [...row]);
        matrix[row][cell] = CELL_FLAG.FLAG;
        this.setState({matrix});
    };

    step = () => {
        let to_open = [];
        this.state.matrix.forEach((row, rowIndex) => {
            row.forEach((cell, cellIndex) => {
                if (cell === CELL_FLAG.SAFE_TO_OPEN) {
                    to_open.push({row: rowIndex, cell: cellIndex});
                }

            });
        });
        if (to_open.length) {
            this.openList(to_open);
        } else {
            let matrix = setupMap(this.state.matrix);
            let should_update = false;
            matrix.forEach((row, rowIndex) => {
                row.forEach((cell, cellIndex) => {
                    if (cell !== this.state.matrix[rowIndex][cellIndex]) {
                        should_update = true;
                    }
                })
            });

            if (should_update) {
                this.setState({matrix: matrix});
                return;
            }
            if (this.state.autobotEnabled && matrix.every(row => row.every(cell => isNaN(cell)))) {
                this.open(Math.round(matrix[0].length / 2), Math.round(matrix.length / 2));
                return;
            }

            if (!this.state.autobotEnabled) {
                this.setState({helpRequired: true});
                return;
            }

            if (this.state.matrix.reduce((accum, line) => {
                return accum + line.filter(cell => isNaN(cell)).length;
            }, 0) < (matrix.length * matrix[0].length) * this.state.autobotStopPercent / 100) {
                this.setState({autobotEnabled: false});
                return;
            }

            let to_open = null;
            let maxflags = 0;
            for (let i = 0; i < matrix.length; i++) {
                for (let j = 0; j < matrix[i].length; j++) {
                    if (isNaN(matrix[i][j])) {
                        let siblingFlags = getSiblings(matrix, i, j, CELL_FLAG.MINE, false);
                        if (siblingFlags > maxflags) {
                            maxflags = siblingFlags;
                            to_open = {
                                row: i,
                                cell: j
                            }
                        }
                    }
                }
            }
            if (to_open) {
                this.open(to_open.cell, to_open.row);
                return;
            }
            for (let i = 0; i < matrix.length; i++) {
                for (let j = 0; j < matrix[i].length; j++) {
                    if (matrix[i][j] === CELL_FLAG.FLAG) {
                        to_open = {
                            row: i,
                            cell: j
                        };
                        break;
                    }
                }
                if (to_open) {
                    break;
                }
            }
            if (to_open) {
                this.open(to_open.cell, to_open.row);
                return;
            }

            this.setState({helpRequired: true});

        }


    };

    componentDidUpdate() {
        if (this.state.started
            && this.state.matrix.length > 0
            && (this.state.autobotEnabled || !this.state.helpRequired)) {
            this.step();
        }

    }

    render() {
        const minesCount = this.state.matrix.reduce((accum, line) => {
            return accum + line.filter(cell => cell === CELL_FLAG.MINE).length;
        }, 0);

        const lose = this.state.matrix.some(row=>row.some(cell=>cell === CELL_FLAG.REAL_MINE))
         && !this.state.matrix.every(row=>row.every(cell=>cell === CELL_FLAG.REAL_MINE ||  !isNaN(cell)));

        return (
            <div className="App">
                <LevelPassword password={this.state.password}/>
                <hr/>
                <Controls startGame={this.startGame}
                          loose={lose}
                          restartGame={this.restartGame}/>
                <hr/>

                {this.state.started
                    ?
                    <React.Fragment>
                        <MinesCounter minesCount={minesCount}/>
                        <HelpInformer helpRequired={this.state.helpRequired}/>
                    </React.Fragment>
                    : null}
                <Desk matrix={this.state.matrix} open={this.open} setFlag={this.setFlag}/>

            </div>
        );
    }

}

export default App;
