import React from 'react';
import styles from './styles.module.css';
import classnames from 'classnames';
import {ReactComponent as Flag} from './flag.svg';
import {ReactComponent as Mine } from './mine.svg';
import {ReactComponent as MineDetected } from './mine-detected.svg';

class Cell extends React.PureComponent {
    static displayName = 'Cell';

    open = () => {
        this.props.open(this.props.i, this.props.j);
    };

    setFlag = (event) => {
        event.preventDefault();
        this.props.setFlag(this.props.i, this.props.j);
    };

    render() {
        const classes = classnames({
            [styles.cell]:true,
            [styles.safe]: this.props.value === -3,
            [styles.opened]: this.props.value >= 0
        });
        return <span className={classes} onClick={this.open} onContextMenu={this.setFlag}>
                {this.props.value >=0 ? this.props.value :
                 this.props.value === -1 ? <Flag/> :
                 this.props.value === -2  ? <MineDetected/>:
                 this.props.value === '*' ? <Mine/> : ' '
                }
        </span>
    }
}

export default Cell;
