import commands from './commands';

class Realtime {
    constructor(url, callback) {
        this.connection = new WebSocket(url);
        this.connection.addEventListener('message', (message) => {
            const response = message.data.split(':'),
            eventType = response.splice(0,1)[0];
            callback(eventType, response.join(':').trim())
        });
        this.connection.addEventListener('open', () => {
            callback('new','connected');
        });
    }

    createGame(level) {
        this.connection.send(commands.CREATE_GAME(level))
    }

    getMap() {
        this.connection.send(commands.GET_MAP);
    }

    openCell(x, y) {
        this.connection.send(commands.OPEN_CELL(x, y));
    }
}

export default Realtime;
