import React, {PureComponent} from "react";
import Autobot from "./Autobot";
import Loose from "./Loose";
import styles from './styles.module.css';

const PREDEFINED_LEVEL = ['easy', 'normal','hard', 'nightmare'];
export default class Controls extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            level: 1,
            autobotEnabled: false,
            autobotStopPercent: 0
        }
    }

    changeLevel = (event) => {
        const level = event.target.value;
        if (/^[1-9]*$/.test(level)) {
            const parsed = parseInt(level);
            this.setState({level: isNaN(parsed) ? '' : parsed});
        }

    };

    useAutobot = (event) => {
        this.setState({autobotEnabled: event.target.checked});
    }

    setAutobotPercent = (event) => {
        this.setState({autobotStopPercent: event.target.value});
    };

    startGame = () => {
        this.props.startGame(this.state.level, this.state.autobotEnabled, this.state.autobotStopPercent);
    };

    startLevel = (level) => {
        this.setState({level}, ()=>{
            this.startGame()
        });
    };

    render() {
        const levels=[];
        for (let index = 0;index < PREDEFINED_LEVEL.length; index++){
            const level = index + 1;
            levels.push(<button className={styles[PREDEFINED_LEVEL[index]]} onClick={()=>this.startLevel(level)} key={level}>{`Level ${level}`}</button>)
        }
        return <React.Fragment>
            {this.props.loose
                ? <Loose restartGame={this.props.restartGame}/>
                : <React.Fragment>
                    <div>
                        {levels}
                    </div>
                    <hr/>
                    <span>or custom level:</span>
                    <input type="text" placeholder={"1,2,3,4 or other"}
                           onChange={this.changeLevel}
                           value={this.state.level}/>
                    {this.state.level ?
                        <button onClick={this.startGame}>Start</button>
                        : null}
                    <Autobot useAutobot={this.useAutobot} setAutobotPercent={this.setAutobotPercent}
                             autobotEnabled={this.state.autobotEnabled}
                             autobotStopPercent={this.state.autobotStopPercent}/>
                </React.Fragment>
            }

        </React.Fragment>
    }

}
