import React from "react";
export default function Autobot(props){
    return <div>
        Dev mode:
        <div>
            <span>Use random decisions</span> <input type="checkbox" onChange={props.useAutobot}
        checked={props.autobotEnabled}/>
        </div>
        <div>
            <span>Stop autobot on unopened cells %: </span> <input type="number" onChange={props.setAutobotPercent}
                                                                   value={props.autobotStopPercent} min={0} max={100}/>
        </div>
    </div>
}
