import React from "react";
import PropTypes from "prop-types";
import styles from './styles.module.css';
import Cell from "../Cell";

class Desk extends React.PureComponent {
    static displayName = 'Desk';

    static propType = {
        matrix: PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.number))
    };

    open = (cell, row) => {
        this.props.open(row, cell);
    };

    setFlag = (row, cell) => {
        this.props.setFlag(row, cell);
    };

    render() {
        const matrix = this.props.matrix;
        return (
            <div className={styles.desk}>
                {matrix.map((row, indexRow) =>
                    <div key={indexRow} className={styles.row}>
                        {row.map((cell, indexCell) =>
                            <Cell value={cell} key={indexCell} i={indexRow} j={indexCell}
                                  open={this.open}
                                  setFlag={this.setFlag}/>
                        )}
                    </div>
                )}
            </div>
        );

    }
}

export default Desk;
